import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { Payment } from '@models';
import { ENDPOINTS } from '@shared';
import { ApiService } from '@services';
import { Router } from '@angular/router';

@Component({
  selector: 'app-list-payment',
  templateUrl: './list-payment.component.html',
  styleUrls: ['./list-payment.component.scss']
})
export class ListPaymentComponent implements OnInit {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<Object>;
  dataSource: MatTableDataSource<Payment>
  displayedColumns: string[] = ['referenceCode', 'typeOfApplication', 'brand', 'typeOfMedium', 'companyName', 'paymentStatus'];
  paymentList = [];
  pageSize = 10;
  pageNo = 0;
  currentPage = 1;
  totalItems = 0;
  pageSizeOptions: number[] = [10, 20, 50];

  constructor(
    private router: Router,
    private apiService: ApiService
  ) { }

  ngOnInit(): void {
    this.getS1PaymentList();
  }

  getS1PaymentList() {
    let endpoint = `?page=${this.currentPage}&size=${this.pageSize}`;
    this.apiService.findAll(ENDPOINTS.accountingPaymentForApprovals + endpoint).subscribe((res: any) => {
      this.paymentList = res.responseData.data;
      console.log('payment list:: ', this.paymentList);

      this.dataSource = new MatTableDataSource<Payment>(this.paymentList);
      // this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
    console.log('Whuy', this.paymentList)
  }

  pageEvents(event: any) {
    this.pageSize = event.pageSize;

    if (event.pageIndex > this.pageNo) {
      this.pageNo++;
    } else {
      this.pageNo--;
    }

    if (event.pageSize > event.length) {
      this.pageNo = 0;
    }

    this.getS1PaymentList();
  }

  getPaymentDetails(id) {
    this.router.navigate(['asc/page/accounting/update/', id]);
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
   }



}
